.viewbody {
    background-color: #EBE7E7; /* Color de fondo */
    padding: 1rem;
    width: 100vw; /* Ajustar el ancho al viewport */
    min-height: 100vh; /* Para ocupar toda la altura de la pgina */
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    padding-top: 0px;
  }
  .historial-citas-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    padding: 0 20px; 
}

  .citas-view {
    width: 85%;
    max-width: 1200px; /* Limitar el ancho mximo */
    background-color: #ffffff; /* Fondo blanco para el contenido */
    border-radius: 8px; /* Bordes redondeados */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Sombra sutil */
    padding: 20px;
    margin-top: .5rem; /* Espaciado superior */
  }
  
  .citas-view h1 {
    font-size: 2rem; /* Tamao de fuente mayor */
    margin-bottom: 2rem; /* Espaciado inferior */
    color: #0a0a0a; /* Color del texto */
    background-color: #A1BEC7; /* Fondo de encabezado */
    padding: 10px;
    border-radius: 4px;
    font-family: 'Josefin Slab', serif; /* Fuente personalizada */
  }
  
  .citas-count {
    margin-bottom: 1.5rem;
    font-size: 1rem;
    color: #666666;
    font-family: 'Josefin Slab', serif; /* Fuente personalizada */
  }
  
  .search-bar-cv {
    display: flex;
    align-items: center;
    display: flex;
    align-items: center;
    flex-grow: 2;
    padding: 0.25rem;
    background-color: #D9D9D9;
    border: 1px solid #ced4da;
    border-radius: 6px;
    height: 2rem;
    width: 80%;
    margin-top: 4rem;
}
  
  .search-bar-cv input {
    padding: 0.35rem;
    border: 1px solid #fffdfd;
    border-radius: 4px;
    margin-left: 0.4rem;
    width: 47rem;
  }
  .search-bar-cv .search-icon-cv {
    margin-left: 0.5rem;
    color: #fefdfd;
}
  .view-table {
    width: 100%;
    border-collapse: collapse;
    background-color: #EBE7E7;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Sombra sutil */
  }
  
  .view-table th,
  .view-table td {
    padding: 10px;
    border: none;
    text-align: center;
    background-color: #EBE7E7;
  }
  
  .view-table th {
    background-color: #A1BEC7; /* Color de fondo de los encabezados */
    color: rgb(9, 9, 9); /* Color del texto en los encabezados */
    font-family: 'Josefin Slab', serif; /* Fuente personalizada */
  }
  
  .view-table tr:nth-child(even) {
    background-color: #f2f2f2; /* Color de fondo de filas alternas */
    font-family: 'Josefin Slab', serif; /* Fuente personalizada */
    color: #000; /* Color del texto */
  }
  
  .view-table tr:hover {
    background-color: #e2e6ea; /* Color de fondo al pasar el ratn */
  }
  
  .btn-details {
    background-color: #92a0a5;
    color: white;
    border: none;
    padding: 10px 20px; /* Botn ms grande */
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px; /* Tamao de fuente del botn */
    font-family: 'Josefin Slab', serif; /* Fuente personalizada */
  }
  
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content {
    background: #fff;
    border-radius: 8px;
    padding: 20px;
    width: 80%;
    max-width: 600px;
    position: relative;
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .close-icon {
    cursor: pointer;
  }
  
  .modal-body {
    max-height: 400px;
    overflow-y: auto;
  }
  